.events {
  background-color: $textColorGrey;

  &__container {
    padding: 50px 0 80px;

    @include onIPad {
      padding: 60px 0 120px;
    }

    @include onDesktop {
      padding: 80px 0 184px;
    }
  }

  &__section-container {
    @include onDesktop {
      margin-bottom: 77px;
    }
  }

  &__info {
    display: flex;
  }

  &__numbers {
    display: none;

    @include onIPad {
      display: flex;
      flex-direction: column;
      margin-right: 30px;
    }
  }

  &__date-number {
    display: flex;
    align-self: end;
    margin-bottom: 10px;

    font-family: $secondary-font;
    font-weight: bold;
    font-size: 36px;
    line-height: 100%;
    color: $colorOrange;

    @include onDesktopLg {
      align-self: start;
      width: 170px;
      font-size: 55px;
    }
  }

  &__time-number {
    align-self: end;
    font-family: $secondary-font;
    font-weight: bold;
    font-size: 24px;
    line-height: 100%;
    color: $colorOrange;

    @include onDesktopLg {
      align-self: end;
      font-size: 36px;
    }
  }

  &__article {
    margin-bottom: 60px;
  }

  &__schedule {
    margin-bottom: 20px;

    @include onIPad {
      margin-bottom: 10px;
    }
  }

  &__date {
    margin-right: 20px;
    color: $colorOrange;

    @include onIPad {
      display: none;
    }
  }

  &__article-heading {
    margin-bottom: 20px;

    font-family: $secondary-font;
    font-weight: bold;
    font-size: 24px;
    line-height: 125%;
    color: $textColorPrimary;

    @include onDesktop {
      margin-bottom: 8px;
    }
  }

  &__article-paragraph {
    margin-bottom: 40px;

    @include onIPad {
      margin-bottom: 30px;
    }

    @include onDesktop {
      margin-bottom: 60px;
    }
  }

  &__section-img {
    display: none;

    @include onDesktop {
      display: inline-block;
    }
  }
}
